<template>
  <el-dialog
    :title="title"
    top="7vh"
    :visible="value"
    :show-close="true"
    width="920px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="handleCancel"
    @open="initSelect"
  >
    <el-scrollbar class="scrollbar-sty">
      <div class="photo-collecting">
        <video ref="video" width="400" height="320" autoplay />
        <canvas ref="canvas" width="400" height="320" />
      </div>
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="handleSnap">拍 照</el-button>
      <el-button type="primary" icon="el-icon-refresh" @click="handleSwitch" />
      <el-button type="primary" @click="handleConfirm">上 传</el-button>
      <el-button @click="handleCancel">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import { uploadApi } from '@/apis/modules/common'
export default {
  name: 'CameraDialog',
  mixins: [],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '拍照上传'
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      constraints: {}, // 请求摄像头参数
      videoStream: {},
      cameraFile: {},
      cameraFlag: '1' // 0代表前置摄像头 1代表后置摄像头
    }
  },
  methods: {
    initSelect() {
      this.init()
    },
    init() {
      this.$nextTick(() => {
        if (this.cameraFlag === '0') {
          // 在移动设备上面，表示优先使用前置摄像头（如果有的话）
          this.constraints = { video: { facingMode: 'user' }}
        } else {
          // 在移动设备上面，表示强制使用后置摄像头（如果有的话）
          this.constraints = { video: { facingMode: { exact: 'environment' }}}
        }
        // const checkCamera = async() => {
        //   const navigator = window.navigator.mediaDevices
        //   const device = await navigator.enumerateDevices() // 返回值为promise类型，用来查看当前媒体设备是否存在
        //   console.log('device: ', device)
        //   return device
        // }
        // checkCamera()
        // 检查浏览器是否支持getUserMedia
        if (navigator.mediaDevices.getUserMedia) {
          // 调用navigator.mediaDevices.getUserMedia方法开启摄像头
          navigator.mediaDevices.getUserMedia(this.constraints)
            .then(stream => {
              this.videoStream = stream
              const video = this.$refs.video
              video.srcObject = stream
            })
            .catch(err => {
              this.$notify.error({
                title: '警告',
                message: '获取摄像头失败'
              })
              console.log('获取摄像头失败：' + err)
            })
        } else {
          console.error('你的浏览器不支持getUserMedia')
        }
      })
    },
    // 拍照
    handleSnap() {
      const video = this.$refs.video
      if (!video) return
      const canvas = this.$refs.canvas
      const context = canvas.getContext('2d')
      context.imageSmoothingEnabled = false // 禁用图像平滑处理，避免图像模糊
      context.drawImage(video, 0, 0, 400, 320)
      this.conversionPicFile(canvas)
    },
    // 切换摄像头
    handleSwitch() {
      // 通过保存下来的stream流对象来关闭摄像头
      if (this.videoStream && this.videoStream.getVideoTracks) {
        // 关闭时，‌通过循环stream流对象的getVideoTracks()方法对里面每个内容调用stop()方法来实现
        const list = this.videoStream.getVideoTracks()
        list.forEach(item => item.stop())
      }
      this.videoStream = {}
      this.cameraFile = {}
      this.cameraFlag === '0' ? (this.cameraFlag = '1') : (this.cameraFlag = '0') // 0代表前置摄像头 1代表后置摄像头
      this.init()
    },
    // canvas转为图片文件
    conversionPicFile(canvas) {
      if (!canvas) return
      // 将Canvas转换为图片格式（通常是PNG）
      canvas.toBlob(blob => {
        // 创建一个新的File对象
        const file = new File([blob], 'screenshot.png', {
          type: 'image/png',
          lastModified: Date.now()
        })
        this.cameraFile = file
      }, 'image/png')
    },
    // 获取用户浏览器信息
    getBrowserInfo() {
      return `Browser: ${navigator.userAgent}`
    },
    // 获取用户操作系统信息
    getOSInfo() {
      const userAgent = navigator.userAgent
      console.log('userAgent: ', userAgent)
      const platform = navigator.platform
      console.log('platform: ', platform)
      if (platform.toLowerCase().indexOf('win') > -1) return 'Windows'
      if (platform.toLowerCase().indexOf('mac') > -1) return 'MacOS'
      if (platform.toLowerCase().indexOf('linux') > -1) return 'Linux'
      if (/iPad|iPhone|iPod/.test(userAgent)) return 'iOS'
      if (/Android/.test(userAgent)) return 'Android'
      return 'Unknown OS'
    },
    // 获取用户是否使用手机
    isMobileDevice() {
      return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)
    },
    // 取消
    handleCancel(done) {
      // 通过保存下来的stream流对象来关闭摄像头
      if (this.videoStream && this.videoStream.getVideoTracks) {
        // 关闭时，‌通过循环stream流对象的getVideoTracks()方法对里面每个内容调用stop()方法来实现
        const list = this.videoStream.getVideoTracks()
        list.forEach(item => item.stop())
      }
      this.videoStream = {}
      this.cameraFile = {}
      this.cameraFlag = '1' // 0代表前置摄像头 1代表后置摄像头
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    handleConfirm() {
      if (!this.cameraFile) {
        this.$notify.warning({
          title: '警告',
          message: '请先拍照'
        })
        return
      }
      // 使用FormData创建一个新的key并附加文件
      const formData = new FormData()
      formData.append('file', this.cameraFile)
      uploadApi('/wms/file/upload', formData)
        .then(res => {
          const { code, data } = res
          if (!code) {
            this.$emit('submit', data)
            this.handleCancel()
          }
        })
        .catch(err => {
          console.log('err: ', err)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.scrollbar-sty {
    height: 350px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
::v-deep .el-dialog__body {
    padding-top: 10px;
    padding-bottom: 0;
}
.photo-collecting {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
</style>
