<template>
  <div class="app-container">
    <div class="my-row">
      <el-button @click="handleGoBack">返回</el-button>
      <div>
        <el-button type="primary" style="margin-right: 15px;" @click="handleCamera">拍照上传</el-button>
        <form-upload-file
          ref="uploadFile"
          btn-content="上传"
          btn-icon="el-icon-upload"
          :show-file-list="false"
          :limit="1"
          :upload-file-list="uploadFileList"
          @uploadFileSuccess="handleUploadSuccess"
          @uploadFileError="handleUploadFileError"
          @openLoading="openLoading"
          @closeLoading="closeLoading"
        />
      </div>
    </div>
    <el-row>
      <el-descriptions :column="3" :label-style="{fontWeight: 900}" :content-style="{paddingRight: '10px',alignItems: 'center'}">
        <el-descriptions-item label="交接单号">{{ baseInfo.no || '---' }}</el-descriptions-item>
        <el-descriptions-item label="检验单号">{{ baseInfo.inspectNo || '---' }}</el-descriptions-item>
        <el-descriptions-item label="物料项数">{{ baseInfo.materialCount || '---' }}</el-descriptions-item>
        <el-descriptions-item label="物料总数">{{ baseInfo.materialTotal || '---' }}</el-descriptions-item>
        <el-descriptions-item label="创建时间">{{ baseInfo.createDate || '---' }}</el-descriptions-item>
        <el-descriptions-item label="接收人">{{ baseInfo.recipientName || '---' }}</el-descriptions-item>
      </el-descriptions>
    </el-row>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="240"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <!-- <el-button type="primary" @click="handleModify(scope.row)">编辑</el-button> -->
          <el-button type="primary" icon="el-icon-view" @click="handlePreview(scope.row)">查看</el-button>
          <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>

    <!-- 新增 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      @submit="init"
    />
    <camera-dialog v-model="cameraVisible" @submit="handleSubmit" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import { documentListApi as getListApi, uploadDocumentApi, getInspectNoDetailApi, deleteDocApi } from '@/apis/modules/in-storage/inspection'
import { formUploadFilesMixins } from '@/mixins/upload'
import axios from 'axios'
import { downloadFile } from '@/utils/utils'
import CameraDialog from './Camera'

export default {
  name: 'InStorageInspectionDocument',
  components: {
    EditDialog,
    CameraDialog
  },
  mixins: [paginationPage, formUploadFilesMixins],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'fileRealName',
          label: '文件名称',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'suffix',
          label: '文件后缀',
          align: 'center',
          minWidth: '200',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'createDate',
          label: '上传时间',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      dialogForm: {},
      supplierPullList: [],
      editType: 'add',
      dialogVisible: false,
      cameraVisible: false,
      baseInfo: {},
      screenLoading: null
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    },
    fileIds() {
      return this.getFileIdList().join()
    }
  },
  mounted() {
    this.init()
    this.getInspectNoDetail()
  },
  methods: {
    init() {
      console.log('init method')
      this.loading = true
      const params = {
        ...this.query,
        id: this.$route.params.id
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data.map(item => {
              const fileUrl = `${process.env.VUE_APP_BASE_API_WMS}${item.fileUrl}`
              const fileName = item.fileRealName
              return {
                ...item,
                fileUrl,
                fileName
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 获取检验单详情
    getInspectNoDetail() {
      return new Promise((resolve, reject) => {
        getInspectNoDetailApi({ id: this.$route.params.id })
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.baseInfo = data
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 上传档案
    handleUploadDocument() {
      return new Promise((resolve, reject) => {
        uploadDocumentApi({ id: this.$route.params.id, fileIds: this.fileIds })
          .then(res => {
            const { code } = res
            if (!code) {
              this.resetFormUploadFilesMixins() // 清空文件列表
              this.init() // 刷新列表
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
            console.log('err', err)
          })
      })
    },
    // 文件上传成功
    handleUploadSuccess(fileInfo) {
      this.uploadFileList.push(fileInfo)
      this.$notify.success({
        title: '成功',
        message: '上传成功'
      })
      this.handleUploadDocument()
    },
    // 附件上传失败
    handleUploadFileError() {
      this.$notify.error({
        title: '错误',
        message: '上传失败'
      })
    },
    openLoading() {
      if (!this.screenLoading) {
        this.screenLoading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        })
      }
    },
    closeLoading() {
      if (this.screenLoading) {
        this.screenLoading.close()
        this.screenLoading = null
      }
    },
    // 预览
    handlePreview(item) {
      const fileName = item.fileName
      if (!fileName) return
      const fileSuffix = fileName.substring(fileName.lastIndexOf('.') + 1)
      const isImg = ['png', 'jpg', 'jpeg', 'bmp', 'gif'].includes(fileSuffix)
      const isTxt = ['txt'].includes(fileSuffix)
      if (isImg) {
        this.$viewerApi({
          options: {
            toolbar: false,
            navbar: false,
            title: false
          },
          images: [item.fileUrl]
        })
      } else if (isTxt) {
        axios.get(item.fileUrl, { responseType: 'blob' })
          .then(res => {
            console.log(res, 'res')
            downloadFile(item.fileName, res.data)
          })
      } else {
        window.open(item.fileUrl)
      }
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除文档信息, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        deleteDocApi({
          id: this.$route.params.id,
          fileIds: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 下载
    handleDownload() {

    },
    // 返回
    handleGoBack() {
      // 删除当前页面路由
      this.$store.dispatch('tagsView/delView', this.$route)
      // 跳转到物资检验页面
      this.$router.push({ name: 'InStorageInspection' })
    },
    handleCamera() {
      this.cameraVisible = true
    },
    handleSubmit(cameraFileInfo) {
      return new Promise((resolve, reject) => {
        uploadDocumentApi({ id: this.$route.params.id, fileIds: cameraFileInfo.id })
          .then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '成功',
                message: '上传成功'
              })
              this.init() // 刷新列表
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
            console.log('err', err)
          })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.my-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
</style>
