<template>
  <div style="display: inline-block;">
    <slot name="pic" />
    <el-upload
      ref="upload"
      sizi="mini"
      :action="`${BASE_API}${url}`"
      :headers="headers"
      :accept="accept"
      :before-upload="beforeUpload"
      :on-remove="handleRemove"
      :before-remove="beforeRemove"
      :multiple="limit > 1"
      :limit="limit"
      :on-exceed="handleExceed"
      :file-list="fileList"
      :on-preview="handlePreview"
      :show-file-list="showFileList"
      :on-success="handleUploadSuccess"
      :disabled="disabled"
      :on-progress="handleUploadProgress"
      :on-error="handleError"
    >
      <el-button v-if="btnContent" type="primary" size="mini" :icon="btnIcon" :disabled="disabled">
        {{ btnContent }}
      </el-button>
      <el-button v-else type="primary" size="mini" :icon="btnIcon" :disabled="disabled" />
      <slot name="tip" />
    </el-upload>
  </div>
</template>

<script>
import $store from '@/store'
import { deleteUploadedFileCommonApi } from '@/apis/modules/common'

export default {
  name: 'FormUploadFileComs',
  props: {
    uploadFileList: {
      type: Array,
      default: () => {
        return []
      }
    },
    showFileList: {
      type: Boolean,
      default: true
    },
    btnIcon: {
      type: String,
      default: ''
    },
    btnContent: {
      type: String,
      default: '点击上传'
    },
    limit: {
      type: Number,
      default: 1
    },
    url: {
      type: String,
      default: '/wms/file/upload'
    },
    accept: {
      type: String,
      default: ''
    },
    limitSize: {
      type: Number,
      default: 99999999999999999999999
    },
    errDefaultMsg: {
      type: String,
      default: '文件上传失败'
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    const token = `${$store.getters.token}`
    return {
      fileList: [],
      // 文件上传
      BASE_API: process.env.VUE_APP_BASE_API_WMS,
      headers: { 'Authorization': token },
      uploadDisabled: false,
      list: []
    }
  },
  watch: {
    uploadFileList: {
      handler(newVal) {
        this.fileList = newVal.map(item => {
          const { fileName, fileUrl, ...info } = item
          return {
            ...info,
            url: fileUrl,
            name: fileName
          }
        })
      },
      immediate: true
    }
  },
  methods: {
    beforeUpload(file) {
      const isLt = file.size <= this.limitSize * 1000
      if (!isLt) {
        this.$message({
          message: `上传大小不能超过 ${this.limitSize}k!`,
          type: 'error'
        })
      }
      if (isLt) {
        this.$emit('openLoading')
      }
      return isLt
    },
    handleUploadProgress(event, file, fileList) {
      this.$emit('uploadFileProgress', event, file, fileList)
    },
    handleError(err, file, fileList) {
      console.log('上传失败', err)
      // 这里可以进行错误处理，如给用户提示或者重试上传等
      setTimeout(() => {
        this.$notify.error({
          title: '错误',
          message: '上传失败'
        })
        this.$emit('closeLoading')
      }, 500)
    },
    handleUploadSuccess(res, file, fileList) {
      this.$emit('closeLoading')
      if (res === '导入成功') {
        this.$emit('uploadFileSuccess')
        return
      }
      const { code, data } = res
      if (code && code === 999) {
        this.$notify.error({
          title: '错误',
          message: res.msg ? res.msg : this.errDefaultMsg
        })
        return
      }
      if (!code) {
        if (!data.id) {
          this.$emit('uploadFileSuccess')
          return
        }
        // Tip 接口返回字段 classification.downloadUrl.fileName.filePath.fileRealName.fileType.fileUrl.id.orderBy.size.suffix
        const { id, fileUrl, fileType, downloadUrl, fileName, suffix } = data
        const fileInfo = {
          id,
          fileId: id,
          fileUrl,
          fileType,
          downloadUrl,
          suffix,
          fileName,
          name: fileName,
          url: fileUrl,
          classification: ''
        }
        this.fileList.push(fileInfo)
        this.$emit('uploadFileSuccess', fileInfo)
      } else {
        this.$refs.upload.clearFiles()
        // 上传失败
        this.$emit('uploadFileError')
      }
    },
    // 删除
    handleRemove(file, fileList) {
      const { ...fileInfo } = file
      if (!fileInfo.id && fileInfo.id !== 0)fileInfo.id = fileInfo.fileId
      // console.log(fileInfo, fileList)
      this.fileList = this.fileList.filter(item => item.id !== fileInfo.id)
      this.$emit('handleRemove', { ...fileInfo })
      // 删除接口
      return new Promise((resolve, reject) => {
        deleteUploadedFileCommonApi({
          id: fileInfo.id
        }).then(res => {
          const { code } = res
          if (code === 0) {
            // this.fileList = this.fileList.filter(item => item.id !== fileInfo.id)
            // this.$emit('handleRemove', { ...fileInfo })
            resolve()
          } else {
            reject()
          }
        }).catch(err => {
          reject(err)
        })
      })
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'cancel-button-class',
        confirmButtonClass: 'confirm-button-class',
        type: 'warning',
        lockScroll: false
      })
    },
    handlePreview(file) {
      const fileId = file.id
      window.open(`${process.env.VUE_APP_BASE_API_WMS}/wms/file/download/${fileId}`)
    },
    handleExceed(files, fileList) {
      this.$notify({
        title: '警告',
        message: `当前限制上传附件数量是 ${this.limit} 个文件`,
        type: 'warning'
      })
    }
  }
}
</script>

<style lang='css' scoped>
</style>
