<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="附件" prop="fileIds">
          <form-upload-file
            :upload-file-list="uploadFileList"
            :limit="1"
            @uploadFileSuccess="uploadFileSuccess"
            @handleRemove="handleRemove"
          />
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>

  </el-drawer>
</template>

<script>
import { updateListApi } from '@/apis/modules/in-storage/purchase/document'
import { formUploadFilesMixins } from '@/mixins/upload'

const INIT_FORM = () => ({
  fileIds: '' // 值为附件列表id拼接的字符串
})

export default {
  name: 'InStorageDocumentCom',
  components: {
  },
  mixins: [
    formUploadFilesMixins
  ],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      loading: false,
      rules: {},
      selectInfo: {
        list: [],
        visible: false,
        radio: ''
      }
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
          this.form.fileIds && this.initUploadFileList(this.form.fileIds.split(','))
        }
      },
      immediate: true
    }
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = false
          if (this.editType !== 'add') {
            updateListApi({
              id: this.form.id,
              fileIds: this.form.fileIds
            }).then(res => {
              const { code } = res
              if (!code) {
                this.$message({
                  message: `${this.title}成功`,
                  type: 'success'
                })
                this.$emit('submit')
                this.beforeClose()
              }
              this.loading = false
            }).catch(err => {
              console.log(err)
              this.loading = false
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 上传成功后  上传组件返回的上传文件信息
    uploadFileSuccess(file) {
      this.uploadFileList.push(file)
      this.form.fileIds = this.getFileIdList().join()
    },
    initSelect() {

    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.form = INIT_FORM()
      this.resetFormUploadFilesMixins()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM })
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
}
::v-deep:focus {
  outline: 0;
}
.scroll-drawer {
  height: calc(100vh - 150px);
  // overflow: auto;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
::v-deep .el-drawer {
  width: 640px;
}
.drawer-footer{
  text-align: right;
}

::v-deep .el-upload-list__item.is-success.focusing .el-icon-close-tip {
  display: none !important;
}

</style>

